
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.flex-grow {
  animation: fadeIn 0.5s ease-in-out;
}

button {
  transition: background-color 0.3s ease;
}

input {
  transition: border-color 0.3s ease;
}

input:focus {
  border-color: #3b82f6; 
  outline: none;
}
